import uniGrid from "@/components/uni-grid/uni-grid.vue"
import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue"
import {mapState,mapMutations} from 'vuex'
export default {
	data() {
		return {
			title: 'Hello',
			myCode:'',
			sessionId:'',
			myData:[],
		}
	},
	onLoad() {
		this.init()
	},
	computed: mapState(['hasLogin','uerInfo']),
	methods: {
		...mapMutations(['login']),
		init(){
			uni.getStorage({
				key:'userInfo',
				success:(res)=>{
					this.login(res.data);
					this.sessionId = res.data.sessionId
					uni.request({
						url:'https://www.nssnail.com/wx/getActivities',
						withCredentials:true,
						data:{
							sessionId:this.sessionId
						},
						success: (res) => {
							if(res.data.stat == 1){
								this.myData = res.data.data
								this.myData.forEach((item)=>{
									if(item.stat == 1){
										item.stat = '人数未满'
									}else if(item.stat == 2){
										item.stat = '人数已满'
									}else if(item.stat == 3){
										item.stat = '已完成'
									}else{
										item.stat = '已取消'
									}
								})
							}else if(res.data.stat == 3){
								uni.showModal({
								    title: '提示',
								    content: '登录过期，请重新登录',
								    success: function (res) {
								        if (res.confirm) {
								            uni.navigateTo({
								            	url:'../myInit/myInit'
								            })
								        }
								    }
								});
							}
						}
					})
				}
			})
			
		},
		
		// 跳转至详情页
		gotoDetail(item){
			var id = item.id
			uni.navigateTo({
				url:'../activityDetail/activityDetail?id='+id+'&sessionId='+this.sessionId
			})
		},
		
		// 跳转至购买页面
		gotoBuy(){
			uni.navigateTo({
				url:'../allGoods/allGoods?sessionId='+this.sessionId
			})
		},
		
		// 跳转至募捐公告页面
		gotoNotice(){
			uni.navigateTo({
				url:'../donateNotice/donateNotice?sessionId='+this.sessionId
			})
		},
		
		// 跳转至活动页面
		gotoDonate(){
			uni.navigateTo({
				url:'../activity/activity?sessionId='+this.sessionId
			})
		}
	},
	components: {
		uniGrid,
		uniGridItem
	}
}
